:root {
  --clr-white: hsl(0, 0%, 100%);
  --black: 0 0% 0%;
  --blue-mod: hsl(238, 40%, 52%);
  --red-sof:hsl(358, 79%, 66%);
  --blue-ligh: hsl(239, 57%, 85%);
  --red-pale: hsl(357, 100%, 86%);
  --blue-dark: hsl(212, 24%, 26%);
  --blue-grayish: hsl(211, 10%, 45%);
  --ligh-gray: hsl(223, 19%, 93%);
  --str-lt-gray: hsl(228, 33%, 97%);
  --ff-arial-sans: Arial, sans-serif;
  --ff-rubik-sans: "Rubik", sans-serif;
  --fs-900: 5rem;
  --fs-800: 3.5rem;
  --fs-700: 3rem;
  --fs-600: 2.5rem;
  --fs-500: 2rem;
  --fs-400: 1.5rem;
  --fs-350: 1.2rem;
  --fs-300: 1rem;
  --fs-200: 0.9375rem;
  --fs-100: 0.875rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: var(--ff-rubik-sans);
  font-size: var(--fs-300);
  color: var(--blue-grayish);
  line-height: 1.5;
  background-color: var(--str-lt-gray);
}

a, a:visited {
  text-decoration: none;
}

h1,
h2,
h3 {
  line-height: 1.1;
}

img,
picture {
  display: block;
}

input,
button,
textarea,
select {
  padding: 0;
  border: 0;
  font: inherit;
  background-color: transparent;
}

button:hover {
  cursor: pointer;
}

.blue-mod {
  color: var(--blue-mod);
}

.red-sof {
  color: var(--red-sof);
}

.blue-ligh {
  color: var(--blue-ligh);
}

.red-pale {
  color: var(--red-pale);
}

.blue-dark {
  color: var(--blue-dark);
}

.blue-grayish {
  color: var(--blue-grayish);
}

.ligh-gray {
  color: var(--ligh-gray);
}

.str-lt-gray {
  color: var(--str-lt-gray);
}

.bg-blue-mod {
  background-color: var(--blue-mod);
}

.bg-red-sof {
  background-color: var(--red-sof);
}

.bg-blue-ligh {
  background-color: var(--blue-ligh);
}

.bg-red-pale {
  background-color: var(--red-pale);
}

.bg-blue-dark {
  background-color: var(--blue-dark);
}

.bg-blue-grayish {
  background-color: var(--blue-grayish);
}

.bg-ligh-gray {
  background-color: var(--ligh-gray);
}

.bg-str-lt-gray {
  background-color: var(--str-lt-gray);
}

.ff-arial {
  font-family: var(--ff-arial-sans);
}

.ff-rubik {
  font-family: var(--ff-rubik-sans);
}

.fs-900 {
  font-size: var(--fs-900);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-350 {
  font-size: var(--fs-350);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-200 {
  font-size: var(--fs-200);
}

.fs-900,
.fs-800,
.fs-700,
.fs-600 {
  line-height: 1.1;
}

.fs-900,
.fs-800,
.fs-700 {
  font-weight: 700;
}

.fs-bold {
  font-weight: bold;
}

.container {
  max-width: 45.625rem;
  margin-top: 2rem;
  margin-inline: auto;
}
@media (max-width: 47.9375em) {
  .container {
    width: 95%;
  }
}

.flex {
  display: flex;
  gap: var(--gap, 1rem);
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}

.nested-inputBox {
  grid-template-columns: max-content 1fr;
}

.flow > * + * {
  margin-top: 1.5rem;
}

.pd-full {
  padding: 0.9375rem;
}
@media (min-width: 35em) {
  .pd-full {
    padding: 1.5rem;
  }
}

main {
  flex-direction: column;
}

.custom-btn {
  --gap: 10px;
  align-items: center;
  padding: 5px;
}

.send-update-btn {
  grid-area: submit-btn;
  justify-self: end;
  padding: 0.6rem 1rem;
  border: 0;
  border-radius: 0.5rem;
  color: #fff;
  background-color: #5357b6;
}

.edit-text-box {
  border: 1px solid #c7c7c7;
  border-radius: 0.5rem;
  font-size: 1rem;
  outline: 0;
  padding: 1rem;
  overflow: hidden;
  resize: none;
}

.overlay {
  display: none;
  position: fixed;
  inset: 0;
  background: hsl(var(--black)/0.6);
  z-index: 99;
}
.overlay.open {
  display: block;
}
.overlay .delete-box {
  --gap: 1.1rem;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  width: clamp(21.4375rem, 3.5vw + 21.4375rem, 25rem);
  padding: 1.5rem;
  border-radius: 0.6rem;
  background: var(--clr-white);
  transform: translate(-50%, -50%);
}
.overlay button {
  flex: 1;
  padding-block: 0.8rem;
  border-radius: 0.6rem;
  color: var(--clr-white);
}

.separation-bar {
  border-left: 0.1875rem solid var(--ligh-gray);
  margin-bottom: -1.5rem;
  transform: translateY(-0.6875rem);
}
@media (min-width: 35em) {
  .separation-bar {
    margin-left: 1rem;
  }
}

.card {
  border-radius: 0.625rem;
  background-color: var(--clr-white);
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "username username username" "comment comment comment" "score . user-action";
  align-items: center;
  background-color: var(--clr-white);
}
@media (min-width: 35em) {
  .card {
    grid-template-columns: max-content 1fr max-content;
    grid-template-areas: "score username user-action" "score comment comment";
    --gap: 1rem 1.5rem;
  }
}
.card--edit {
  grid-template-areas: "username username username" "comment comment comment" "score . user-action" ". . submit-btn";
}
@media (min-width: 35em) {
  .card--edit {
    grid-template-areas: "score username user-action" "score comment comment" ". . submit-btn";
  }
}
.card__user {
  grid-area: username;
  align-items: center;
  --gap: 0.6rem;
  font-size: var(--fs-100);
}
@media (min-width: 35em) {
  .card__user {
    --gap: 1rem;
    font-size: var(--fs-300);
  }
}
.card__user img {
  width: clamp(33px, 33px + 2vw, 75px);
}
.card__user .user-reply {
  margin-left: 0.25rem;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  color: var(--clr-white);
  font-weight: 400;
  border-radius: 0.25rem;
  background: var(--blue-mod);
}
.card__comment {
  grid-area: comment;
}
.card__comment .respondedUser {
  font-weight: 600;
  color: var(--blue-mod);
}
.card__comment-score {
  grid-area: score;
  --gap: 0;
  align-items: center;
  justify-content: space-evenly;
  width: 5.625rem;
  padding-block: 0.5rem;
  border: 0;
  border-radius: 0.625rem;
}
@media (min-width: 35em) {
  .card__comment-score {
    align-self: flex-start;
    flex-direction: column;
    --gap: 0.9375rem;
    width: auto;
    padding: 0.75rem 0.625rem;
  }
}
.card__comment-score > .vote-button {
  flex-basis: 16%;
  cursor: pointer;
}
.card__user-btns {
  grid-area: user-action;
  justify-self: end;
}

.card.card--user {
  grid-template-areas: "comment-area comment-area comment-area" "profile-image . submit-btn";
}
@media (min-width: 35em) {
  .card.card--user {
    align-items: start;
    grid-template-areas: "profile-image  comment-area submit-btn";
  }
}
.card__label-image {
  grid-area: profile-image;
  position: relative;
}
.card__label-image img {
  width: clamp(33px, 33px + 2vw, 75px);
}
.card__label-image span {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
}
.card__comment-input {
  grid-area: comment-area;
}